<template>
  <div>
    <div class="year-data">
            <div class="year-font"></div>
            <el-select v-model="value" placeholder="" style="margin-left:160px;" @change="handleCommand">
                <el-option  
                  v-for="item in years"
                  :label="item.label"
                  :value="item.value">
                </el-option>
          </el-select>
        </div>
    <h1 class="head">whois</h1>
    <div id="unknown">
      <span></span>
      <span class="num-class">{{unknown}}</span>
    </div>
    <div id="unknown">
      <span></span>
      <span class="num-class">{{ forein }}</span>
    </div>
    <div id="spa-map"></div>
  </div>
</template>
<script>
  import echarts from "echarts/lib/echarts";
  import "echarts/map/js/china"
  let SpaMap;

  export default{
  //url
  methods: {
  handleCommand(value) {
  $.ajax({
  url:"http://172.29.152.3:8000/stainfo/space/spaceinfo?value="+value,
  dataType:"json",
  type:'GET',
  success:function (result)
  { //
  this.unknown=":"+result.unknow;
  this.forein=":"+result.foreign;
  //
  var xValue=[];
  var yValue=[];
  var i=0;
  var len=result.info.length;
  for(i=0;i < len;i++)
    {
      xValue[i]=result.info[i].name;
      yValue[i]=result.info[i].value;
    }

  SpaMap.setOption({
  series: [{
  name: '',
  data:(function(){
  var res=[];
  for (i = 0; i < len; i++)
                       {
                          res.push({
                           name:xValue[i],
                           value:yValue[i]
                        });  
                       }
                       return res;
                       })()
          }]
  })
  }.bind(this),

  error:function()
  {
  alert("");
  }

  })
  }
  },
  data () {
  return{
  unknown:"",
  forein:"",

  years:[{
  value: '1',
  label: ''
  }, {
  value: '2',
  label: ''
  }, {
  value: '3',
  label: ''
  }, {
  value: '4',
  label: 'IP'
  }, {
  value: '5',
  label: 'ICP'
  }],
  value:''
  }
  },

  mounted() {
  SpaMap = echarts.init(document.getElementById('spa-map'));
  SpaMap.setOption({
  tooltip: {
  trigger: 'item'
  },
  legend: {
  orient: 'vertical',
  left: 'left',
  data:['']
  },
  visualMap: {
  min: 0,
  max: 2500,
  left: 'left',
  top: 'bottom',
  text: ['',''],           // 
  },

  series: [{
  name: '',
  type: 'map',
  layoutCenter: ['50%', '50%'],
  layoutSize: 900,
  mapType: 'china',
  roam: false,
  label: {
  normal: {
  show: true
  },
  emphasis: {
  show: true
  }
  },
  data:[]
  }]
  });
  window.onresize = myChart.resize;
  },
  components: {}
  }
</script>

<style>
  #spa-map{
  margin-top: 30px;
  margin-left: 50px;
  width: 90%;
  height: 720px;
  }
  .head{
  color:#08a9f2;
  text-align:center;
  }
</style>



